.blog {
  background-image: -webkit-gradient(linear, left top, left bottom, from(#f8f8f8), color-stop(#f9faf9), color-stop(#fbfbfb), color-stop(#fcfdfc), to(#fefefe));
  background-image: linear-gradient(to bottom, #f8f8f8, #f9faf9, #fbfbfb, #fcfdfc, #fefefe);
  margin: 0 auto; }
  .blog .col {
    margin-bottom: 20px !important; }
  .blog .card-img-top {
    height: 200px;
    object-fit: cover; }
  .blog .card {
    box-shadow: 3px 3px 5px grey;
