  .blog .blog--section--title {
    text-align: center;
    padding-top: 30px;
    font-weight: bold;
    font-size: 26px; }
  .blog .blog--section--subtitle {
    text-align: center;
    display: block;
    padding-bottom: 30px;
    font-size: 18px;
    color: #73839b; }
  .blog .blog--date {
    font-size: 13px;
    font-weight: bold;
    font-style: italic;
    color: #7b8a9d; }
  .blog .blog--postname {
    font-size: 16px;
    padding-top: 5px; }
  .blog .blog--post--descript {
    color: white;
    text-align: justify !important; }
  .blog .blog--card {
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    position: relative;
    height: 220px;
    width: 100%;
    display: inline-block;
    margin: 0 0 100px 0; }
  .blog .blog--card .blog--card__front,
  .blog .blog--card .blog--card__back {
    border-radius: 10px;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out; }
  .blog .blog--card .blog--card__front {
    -webkit-transform: rotateY(0);
            transform: rotateY(0);
    z-index: 2; }
  .blog .blog--card .blog--card__front .title {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 10px; }
  .blog .blog--card .blog--card__back {
    background: #333;
    -webkit-transform: rotateY(-180deg);
            transform: rotateY(-180deg);
    padding: 20px; }
  .blog .blog--card:hover .blog--card__front {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg); }
  .blog .blog--card:hover .blog--card__back {
    -webkit-transform: rotateY(0);
            transform: rotateY(0); }
  .blog .blog--card__back .btn {
    display: block;
    margin: 0 auto; }
  .blog .blog--card__back a {
    text-decoration: none; }

